import Vue from "vue";
import VueRouter from "vue-router";
import Coupons from "../views/Coupons.vue";

import Home from './../views/home/Home'

const Shop = () => import('./../views/shop/Shop')
const Wenti = () => import('./../views/wenti/Wenti')
const Video = () => import('./../views/video/Video')
const AllShop = () => import('./../views/myTicket/allShop')
// const AllShopAll = () => import('./../views/myTicket/allShopAll')
// const TicketDetails = () =>import('./../views/myTicket/ticketDetails')
Vue.use(VueRouter);

const routes = [
  {
    path:"/",
    redirect: '/coupons'
  },
  {
    path: "/coupons",
    name: "coupons",
    component: Coupons,
    children:[
      {
        path:"/",
        redirect: '/coupons/home'
      },
      {
        path:"home",
        name:"home",
        component:Home
      },
      {
        path:"wenti",
        name:"wenti",
        component:Wenti
      },
      {
        path:"video",
        name:"video",
        component:Video
      },
      {
        path:"allshop",
        name:"allshop",
        component:AllShop
        // component:AllShopAll
      },
      {
        path:"shop",
        name:"shop",
        component:Shop
      }
      // {
      //   path:"ticketdetails",
      //   name:"ticketdetails",
      //   component:TicketDetails
      // }
    ]
  },
];

const router = new VueRouter({
  routes
});

export default router;
