import Axios from 'axios';

let http = {
    get:function(url, params, fn){
        Axios.get(url,{       
            params:params
        }).then((response)=>{
            fn(response)
        }).catch((error)=>{
            console.log(error)
        })
    },
    post:function(url, params, fn){
        Axios.post(url,       
            params
        ).then((response)=>{
            fn(response)
        }).catch((error)=>{
            console.log(error)
        })
    }
}

export default http;